
@font-face {
  font-family: "icomoon";
  src: url("fonts/icomoon.eot?gip8im");
  src: url("fonts/icomoon.eot?gip8im#iefix") format("embedded-opentype"),
    url("fonts/icomoon.ttf?gip8im") format("truetype"),
    url("fonts/icomoon.woff?gip8im") format("woff"),
    url("fonts/icomoon.svg?gip8im#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  font-family: "icomoon", serif !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

